export default "/* css for making entire register component appear below header */\n#signup{\n  padding-top: 4.5rem;\n}\n/* css for the entire register component */\n* {box-sizing: border-box}\ninput[type=email] ,input[type=password] {\n    width: 100%;\n    padding: 12px;\n    margin: 5px 0 10px 0;\n    display: inline-block;\n    border: none;\n    background: #f1f1f1;\n  }\ninput[type=email]:focus,input[type=password]:focus {\n    background-color: #ddd;\n    outline: none;\n  }\nhr {\n    border: 1px solid #f1f1f1;\n    margin-bottom: 10px;\n  }\n/* Set a style for all buttons */\nbutton {\n    background-color: #4CAF50;\n    color: white;\n    padding: 12px 26px;\n    margin: 8px 0;\n    border: none;\n    cursor: pointer;\n    width: 100%;\n    opacity: 0.9;\n  }\nbutton:hover {\n    opacity:1;\n  }\n/* Float  signup buttons and add an equal width */\n.signupbtn {\n\n    width: 100%;\n  }\n/* Add padding to container elements */\n.container {\n    padding: 14px;\n  }\n/* Clear floats */\n.clearfix::after {\n    content: \"\";\n    clear: both;\n    display: table;\n  }\n/* Change styles for c signup button on extra small screens */\n@media screen and (max-width: 300px) {\n     .signupbtn {\n       width: 100%;\n    }\n  }\n@media screen and (max-width: 606px) {\n    #signup{\n      padding-top: 11.5rem;\n      padding-bottom: 5.5rem;\n    }\n }\nlabel {\n    display: block;\n    margin-top: 5px;\n  }\n.card-container.card {     \n    max-width: 400px !important;\n    padding: 28px 36px;\n    margin: auto;\n  }\n.card {\n    background-color: #f7f7f7;\n    padding: 20px 25px 30px;\n    margin: 0 auto 15px;\n    margin-top: 5px;\n    border-radius: 2px;\n    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);\n  }\n.profile-img-card {\n    width: 66px;\n    height: 66px;\n    margin: 0 auto 10px;\n    display: block;\n    border-radius: 50%;\n  }\n.alert-danger{\n   color: #721c24;\n    background-color: #f8d7da;\n    border-color: #f5c6cb;\n    font-size: 1rem;\n    /* font-weight: 400; */\n    line-height: 1.5;\n    color: #212529;\n    text-align: left;\n}\n.alert-warning {\n  color: #856404;\n  background-color: #fff3cd;\n  border-color: #ffeeba;\n}\n.alert {\n  position: relative;\n  padding: .75rem 1.25rem;\n  margin-bottom: 1rem;\n  border: 1px solid transparent;\n  border-radius: .25rem;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"