export default "/* css to make all cards appear below header */\n#userquery{\n  padding-top: 4rem;\n  padding-bottom: 4rem;\n}\n/* css for alignment of cards */\n* {\n    box-sizing: border-box;\n  }\ninput[type=text],input[type=number], select, textarea {\n    width: 100%;\n    padding: 12px;\n    border: 1px solid #ccc;\n    border-radius: 4px;\n    resize: vertical;\n  }\nlabel {\n    padding: 12px 12px 12px 0;\n    display: inline-block;\n  }\ninput[type=submit] {\n    background-color: #4CAF50;\n    color: white;\n    padding: 12px 20px;\n    border: none;\n    border-radius: 4px;\n    cursor: pointer;\n    float: right;\n  }\ninput[type=submit]:hover {\n    background-color: #45a049;\n  }\n.container {\n\n    border-radius: 5px;\n    background-color: #f2f2f2;\n    padding: 20px;\n    left: 1rem;\n    width: 95%;\n    margin: auto;\n    border-radius: 5px;\n    background-color: #f2f2f2;\n    padding: 20px;\n  }\n.col-25 {\n    float: left;\n    width: 25%;\n    margin-top: 6px;\n  }\n.col-75 {\n    float: left;\n    width: 75%;\n    margin-top: 6px;\n  }\n/* Clear floats after the columns */\n.row:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */\n@media screen and (max-width: 600px) {\n    .col-25, .col-75, input[type=submit] {\n      width: 100%;\n      margin-top: 0;\n    }\n    #userquery{\n      padding-top: 12.5rem;\n      padding-bottom: 6rem;\n    }\n    \n  }\n  \n/*# sourceMappingURL=data:application/json;base64, */"